.topbar {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  background-color: #fff;
  height: 70px;
  left: 0;
  position: fixed;
  top: 0;
  transform: translate3d(0, 0, 0);
  width: 100%;
